import React from 'react';
import { Helmet } from 'react-helmet';
export default function dml() {
  return (
    <div>
      <Helmet>
        <title>Neem Tree &mdash; Direct Market Linkage</title>
        <meta
          name='description'
          content='A process where the farmer is given the opportunity to sell their harvest at the nearing Market places on time that will help in curbing the influence of petty traders and middlemen which will help the small farmers especially to gain certain advantage. '
        />

        <meta
          name='twitter:description'
          content='A process where the farmer is given the opportunity to sell their harvest at the nearing Market places on time that will help in curbing the influence of petty traders and middlemen which will help the small farmers especially to gain certain advantage. '
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title='DML'
          src='https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR'
          height='0'
          width='0'
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div>
        <br />
        <br />
        <div className='container'>
          <div className='card mb-3 border-0' style={{ maxWidth: '100%' }}>
            <div className='row no-gutters'>
              <div className='col-lg-7 pt-6 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center'>
                <div className='card-body'>
                  <div className='card-title'>
                    <h1 style={{ fontSize: '49px' }}>Direct Market Linkage</h1>
                    <br />
                    <h3 style={{ fontSize: '29px', color: '#ec7b10' }}>
                      Supply Chain, Farm to Fork Network
                    </h3>
                  </div>
                  <p
                    className='card-text text-justify'
                    style={{
                      fontSize: '22px',
                      marginRight: '20px',
                      marginTop: '40px',
                      color: 'black',
                    }}
                  >
                    An effective process of linking rural farmers to the market,
                    which would facilitate timely marketing of farm produce and
                    help decongest the mandis. Direct market linkage has been
                    acknowledged as the means through which small farmers can
                    gain an advantage in the market that’ll potentially improve
                    the return to investments made by the farmers. A direct
                    market linkage will also help to curb down the influence of
                    petty traders and middlemen.
                  </p>
                </div>
              </div>
              <div className='col-lg-5 order-1 order-lg-2 hero-img'>
                <img
                  src='img/DirectMarketLinkage/Direct Market Linkage.jpg'
                  className='card-img'
                  alt='...'
                  style={{ width: '90%', marginLeft: '20px' }}
                />
              </div>
            </div>
          </div>
          <br />
          <div style={{ textAlign: 'center' }}>
            <ul className='submenu'>
              <li>
                <a
                  href='farmUncle.html'
                  style={{
                    border: '3px solid #dad8d8',
                    background: '#90EE90',
                    padding: '15px',
                    color: 'black',
                  }}
                >
                  Service for consumers
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}



