import React from 'react';
import { Helmet } from 'react-helmet';

export default function FarmUncle() {
  // useEffect(() => {
  // setInterval(() => setPhoto = !photo, 2000);

  // });
  const [photo, setPhoto] = React.useState(true);
  React.useEffect(() => {
    const intervalID = setInterval(() => {
      setPhoto((photo) => !photo);
    }, 2500);
    return () => clearInterval(intervalID);
  }, []);
  const alertStyle = {
    color: '#856404',
    backgroundColor: '#fff3cd',
    borderColor: '#ffeeba',
    fontSize: '3vh',
  };

  const farmUncleTextStyle = {
    display: 'flex',
    height: '25rem',
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: '55vh',
    marginTop: '42vh',
    marginLeft: 'auto',
    marginRight: 'auto',
  };

  return (
    <div>
      <Helmet>
        <title>Farm Uncle &mdash; Grocery Solution</title>
        <meta
          name="description"
          content="A one stop shop for Grocery shopping for our Urban Customers. We take orders on WhatsApp and are live at select locations. Order now from farm uncle to directly source your vegetables from the farmers. "
        />

        <meta
          name="twitter:description"
          content="A one stop shop for Grocery shopping for our Urban Customers. We take orders on WhatsApp and are live at select locations. Order now from farm uncle to directly source your vegetables from the farmers. "
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title="Farm Uncle"
          src="https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR"
          height="0"
          width="0"
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div className="container text-center">
        <img
          src="img/Services/farmUncleLogo.png"
          alt=""
          className="img-fluid"
          style={{ width: '15rem', height: '15rem' }}
        />
        <h1 style={{ fontSize: '8vh' }}>
          <span style={{ color: '#e76f38' }}>Farm Uncle</span>
        </h1>
        <br />
        <div style={alertStyle} className="alert-farmuncle" role="alert">
          We are live at select locations!
        </div>
        <br />
        <br />
      </div>
      <div
        style={{
          display: 'flex',
          height: '70vh',
          justifyContent: 'space-around',
        }}
      >
        <div
          style={{
            alignItems: 'center',
            justifyContent: 'center',
            margin: '0',
          }}
        >
          <img
            style={{
              height: '80%',
              alignItems: 'center',
              justifyContent: 'center',
            }}
            className="farmImgs1"
            src="img/websitedesign.png"
            alt=""
          />
        </div>
        <div>
          <div className="slider" style={{ height: '56rem' }}>
            <div className="text-center slides">
              {photo === true ? (
                <div className="slide first" style={{ marginLeft: '-75px' }}>
                  <img
                    src="img/Farm Uncle/Whatsapp1.png"
                    alt="Services"
                    style={{
                      padding: '2rem 0',
                      width: '24rem',
                      height: '56rem',
                    }}
                    className="img-fluid"
                  />
                </div>
              ) : (
                <div className="slide first" style={{ marginLeft: '-75px' }}>
                  <img
                    src="img/Farm Uncle/Whatsapp2.png"
                    alt="Services"
                    style={{
                      padding: '2rem 0',
                      width: '24rem',
                      height: '56rem',
                    }}
                    className="img-fluid"
                  />
                </div>
              )}
            </div>
          </div>
          <br />
          <a href="https://wa.link/6qlnql">
            <button
              type="button"
              className="btn  ml-5 mr-5 aa1"
              id="order-now-button-unique"
              style={{
                fontWeight: 'bolder',
                backgroundColor: '#70d603',
                boxShadow:
                  '0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19)',
                cursor: 'pointer',
              }}
            >
              ORDER NOW
            </button>
          </a>
        </div>
        <div
          style={{
            alignItems: 'center',
            justifyContent: 'center',
            margin: '0',
          }}
        >
          <img
            style={{ height: '80%', marginTop: '0.5vh' }}
            className="farmImgs1"
            src="img/websitedesign1.png"
            alt=""
          />
        </div>
      </div>

      <div className="container text-center" style={farmUncleTextStyle}>
        <img
          style={{ width: '50%' }}
          className="farmImgs2"
          src="img/websitedesign.png"
          alt=""
        />
        <img
          style={{ width: '50%' }}
          className="farmImgs2"
          src="img/websitedesign1.png"
          alt=""
        />
      </div>
      <div className="text1">
        <div className="container text-center ordering-text">
          <h1
            style={{ fontSize: '3.5vh', color: '#53ce0b', marginTop: '-35vh' }}
          >
            Ordering fresh vegetables for your outlet is
          </h1>
          <h1 style={{ fontSize: '3.4vh', color: '#ec7b10' }}>
            as easy using WhatsApp
          </h1>
        </div>
      </div>
      <div className="text2" style={{ visibility: 'none' }}>
        <div className="container text-center ordering-text">
          <h1 style={{ fontSize: '3.5vh', color: '#53ce0b' }}>
            Ordering fresh vegetables for your outlet is
          </h1>
          <h1 style={{ fontSize: '3.4vh', color: '#ec7b10' }}>
            as easy using WhatsApp
          </h1>
        </div>
      </div>
      <br />
      <br />
      <br />
      <div className="container text-left" id="gallery">
        <h1
          style={{
            fontSize: '4rem',
            marginTop: '5%',
            // textAlign: 'center',
            color: 'orange',
          }}
        >
          Testimonials
        </h1>
        <br />
        <div
          className="container text-left"
          style={{ paddingLeft: '0px', paddingRight: '0px', margin: '0 auto' }}
        >
          <hr style={{ color: '#ec7b10' }} />
          <div
            style={{
              display: 'flex',
              flexWrap: 'wrap',
              justifyContent: 'center',
            }}
            className="container"
          >
            <br />

            <div
              id="myCarousel"
              class="carousel slide"
              data-interval="6000"
              cycle="true"
              style={{ margin: '0 auto' }}
            >
              {/* Indicators */}
              <ol className="carousel-indicators">
                <li
                  data-target="#myCarousel"
                  data-slide-to={0}
                  className="active"
                />
                <li data-target="#myCarousel" data-slide-to={1} />
                <li data-target="#myCarousel" data-slide-to={2} />
                <li data-target="#myCarousel" data-slide-to={3} />
                <li data-target="#myCarousel" data-slide-to={4} />
              </ol>
              {/* Wrapper for slides */}

              <div className="carousel-inner" role="listbox">
                <div className="item active">
                  <img src="./img/Testimonials/farmUncle/1.png" alt="img" />
                </div>
                <div className="item">
                  <img src="./img/Testimonials/farmUncle/2.png" alt="img" />
                </div>
                <div className="item">
                  <img src="./img/Testimonials/farmUncle/3.png" alt="img" />
                </div>
                <div className="item">
                  <img src="./img/Testimonials/farmUncle/4.png" alt="img" />
                </div>
                {/* Left and right controls */}
                <a
                  className="left carousel-control"
                  href="#myCarousel"
                  role="button"
                  data-slide="prev"
                >
                  <span
                    className="glyphicon glyphicon-chevron-left"
                    aria-hidden="true"
                  />
                  <span className="sr-only">Previous</span>
                </a>
                <a
                  className="right carousel-control"
                  href="#myCarousel"
                  role="button"
                  data-slide="next"
                >
                  <span
                    className="glyphicon glyphicon-chevron-right"
                    aria-hidden="true"
                  />
                  <span className="sr-only">Next</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



