import React from 'react';
import { Helmet } from 'react-helmet';
export default function skillDev() {
  return (
    <div>
      <Helmet>
        <title>Neem Tree &mdash; Skill Development</title>
        <meta
          name='description'
          content='Our aim is not only to load the farmers, our most important stakeholders with information but to also make them technologically capable. By doing so our objective is to assist the farmers in making better informed  decisions and witness an increase in production output and their income. '
        />

        <meta
          name='twitter:description'
          content='Our aim is not only to load the farmers, our most important stakeholders with information but to also make them technologically capable. By doing so our objective is to assist the farmers in making better informed  decisions and witness an increase in production output and their income. '
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title='Skill Dev'
          src='https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR'
          height='0'
          width='0'
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div>
        <br />
        <br />
        <div className='container'>
          <div className='card mb-3 border-0' style={{ maxWidth: '100%' }}>
            <div className='row no-gutters'>
              <div className='col-lg-7 pt-6 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center'>
                <div className='card-body'>
                  <div className='card-title'>
                    <h1 style={{ fontSize: '49px' }}>Skill Development</h1>
                    <br />
                    <h3 style={{ fontSize: '29px', color: '#ec7b10' }}>
                      Education and Innovation
                    </h3>
                  </div>
                  <p
                    className='card-text text-justify'
                    style={{
                      fontSize: '22px',
                      marginRight: '20px',
                      marginTop: '40px',
                      color: 'black',
                    }}
                  >
                    The platform to provide farmers with knowledge and
                    assistance that will help them better understand the
                    involved technologies and methods. They can contact to our
                    members live or through a discussion forum to resolve any
                    doubts that they have revolved around the technology. This
                    is a one-time investment that will keep helping the farmers
                    for all their upcoming produce. Skill development in
                    agritech is very important in the present time so that they
                    can take better decisions to expand their production output
                    and their income.
                  </p>
                </div>
              </div>
              <div className='col-lg-5 order-1 order-lg-2 hero-img'>
                <img
                  src='img/Skill Development/Skill Dev.svg'
                  className='card-img bulb'
                  alt='...'
                  style={{ width: '350px' }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



