import React from "react";
import {Helmet} from 'react-helmet';

export default function ContactUs() {
  return (
    <div>
    <Helmet>

    <title>Neem Tree &mdash; Contact Us</title>
    <meta name="description" content="Contact us on connect@neemtreeagrico.in. Find us on social media apps such at Instagram, Twitter Facebook and LinkedIn @neemtreeagrosolutions"/>
   
    <meta name="twitter:description" content="Contact us on connect@neemtreeagrico.in. Find us on social media apps such at Instagram, Twitter Facebook and LinkedIn @neemtreeagrosolutions"/>
    </Helmet>

      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript
      ><iframe
      title="Contact Us"
        src="https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR"
        height="0"
        width="0"
        style={{display: "none", visibility: "hidden"}}
      ></iframe></noscript>
        {/* <!-- End Google Tag Manager (noscript) --> */}
      <div className="container text-center">
        <h1 style={{ fontSize: "69px" }}>
          Get In - <span style={{ color: "#e76f38" }}>Touch</span>
        </h1>
        <br />
   
      <svg height={4} width="80vw">
          <line
            x1={0}
            y1={0}
            x2="100vw"
            y2={0}
            style={{ stroke: "grey", strokeWidth: 4 }}
          />
          Sorry, your browser does not support inline SVG.
        </svg>
        </div>
  <div className="container">
    <div className="row">
     
      <div className="col-lg-6">
        <div className="card">
          <div className="card-body">
            <h5 className="text-center card-title" style={{fontSize: '3.5vw'}}>Email Us</h5>
            <br/>
            <img src="./img/About Us - Menu/Contact Us/Mail.svg" alt="" style={{width: '130px'}} className="mx-auto d-block" />
            <br /><br/>
            <div className="text-center">
              <a href="mailto:neemtreeagrosolutions@gmail.com" className="btn text-center" id="MailNt" style={{color: 'black', fontSize: '1.5vw'}}>neemtreeagrosolutions@gmail.com</a>
            </div>
          </div>
        </div>
      </div>
      <div className="col-lg-6">
        <div className="card" style={{backgroundColor: 'white'}}>
          <div className="card-body">
            <h5 className="text-center card-title" style={{fontSize: '3.5vw'}}>Call Us</h5>
            <br/>
            <img src="./img/About Us - Menu/Contact Us/Phone.svg" alt="" style={{width: '120px'}} className="mx-auto d-block" />
            <div className="text-center">
              <br /><br />
              
              <a href="tel:011-410-550-90" className="btn" style={{color: 'black',fontSize: '2vw'}}>011-410-550-90</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <br /><br />


      <div className="container">
        <svg height={4} width="80vw">
          <line
            x1={0}
            y1={0}
            x2="100vw"
            y2={0}
            style={{ stroke: "rgb(58, 184, 8)", strokeWidth: 5 }}
          />
          Sorry, your browser does not support inline SVG.
        </svg>
      </div>
      <br />
      <br />
      <br />
      <div className="container" style={{ backgroundColor: "rgb(3, 67, 71)" }}>
        <div className="px-5 py-5">
          <div className="row">
            <div className="col-12">
              <h2
                className="contact-title"
                style={{ fontSize: "45px", color: "white", marginTop: "25px" }}
              >
                Have A Query?
              </h2>
              <br />
            </div>
            <br />
            <div className="col-lg-12">
              <form
                className="form-contact contact_form"
                action
                method
                id="contactForm"
                noValidate="novalidate"
              >
                <div className="row">
                  <div className="col-sm-6">
                    <div className="form-group">
                      <input
                        className="form-control valid"
                        name="name"
                        id="name"
                        type="text"
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter your name'"
                        placeholder="Enter your name"
                      />
                    </div>
                  </div>
                  <div className="col-sm-6">
                    <div className="form-group">
                      <input
                        className="form-control valid"
                        name="email"
                        id="email"
                        type="email"
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter email address'"
                        placeholder="Email"
                      />
                    </div>
                  </div>
                  <div className="col-sm-6">
                    <div className="form-group">
                      <input
                        className="form-control"
                        name="subject"
                        id="subject"
                        type="text"
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter Subject'"
                        placeholder="Enter Subject"
                      />
                    </div>
                  </div>
                  <div className="col-sm-6">
                    <div className="form-group">
                      <input
                        className="form-control valid"
                        id="phnumber"
                        type="text"
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter Phone Number'"
                        placeholder="Phone Number"
                      />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="form-group">
                      <textarea
                        className="form-control w-100"
                        name="message"
                        id="message"
                        cols={30}
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter Message'"
                        placeholder=" Enter Message"
                        defaultValue={""}
                      />
                    </div>
                  </div>
                </div>
                <div className="form-group mt-3">
                  <button
                    type="submit"
                    className="button button-contactForm boxed-btn"
                  >
                    Send
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <br />
    </div>
  );
}



