import React from 'react';
import Aos from 'aos';
function BackTop() {
  React.useEffect(() => {
    Aos.init({ duration: 1400 });
  }, []);
  return (
    <div id="back-top" data-aos="fade-in" data-aos-offset="200">
      <a href="#top" title="Go to Top">
        <i className="fas fa-level-up-alt" />
      </a>
    </div>
  );
}

export default BackTop;



