import React from 'react';
import {Helmet} from 'react-helmet';
export default function storage() {
  return (
    <div>

 <Helmet>
 <title>Neem Tree &mdash; Storage</title>
    <meta name="description" content="In order to attain maximum profit at fair prices, we aim to provide the farmers with services wherein they can sell their produce soon after it is harvested and while they are at the best freshness. Thereby eradicating the trouble that farmers usually faced due to lack of storage facilities once the crops are harvested."/>

    <meta name="twitter:description" content="In order to attain maximum profit at fair prices, we aim to provide the farmers with services wherein they can sell their produce soon after it is harvested and while they are at the best freshness. Thereby eradicating the trouble that farmers usually faced due to lack of storage facilities once the crops are harvested."/>
    </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript
      ><iframe
      title="Storage"
        src="https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR"
        height="0"
        width="0"
        style={{display: "none", visibility: "hidden"}}
      ></iframe></noscript>
        {/* <!-- End Google Tag Manager (noscript) --> */}
      <br />
      <br />
      <div className='container'>
        <div className='card mb-3 border-0' style={{ maxWidth: '100%' }}>
          <div className='row no-gutters'>
            <div className='col-lg-7 pt-6 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center'>
              <div className='card-body'>
                <div className='card-title'>
                  <h3 style={{ fontSize: '29px', color: '#ec7b10' }}>
                    <span style={{ fontSize: '49px', color: 'black' }}>
                      Storage
                    </span>
                    <br /> Cold Storage
                  </h3>
                </div>
                <p
                  className='card-text text-justify'
                  style={{
                    fontSize: '22px',
                    marginRight: '20px',
                    marginTop: '40px',
                    color: 'black',
                  }}
                >
                  Storage is one of the biggest problems faced by the farmers,
                  due to the non-availability of enough space compelling them to
                  sell their produce at the earliest.
                  By providing storage facilities to the farmers near their
                  fields and in particular to the small and marginal farmers, we
                  relive them from selling their produce soon after the harvest
                  so that they can wait for favorable market prices and increase
                  their profits.
                </p>
              </div>
            </div>
            <div className='col-lg-5 order-1 order-lg-2 hero-img'>
              <img
                src='img/Cold Storage/Cold Storage.svg'
                className='card-img bus1'
                alt='...'
                style={{ width: '90%', marginTop: '50px', marginRight: '30px' }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



