import React from "react";
function Banner2(props) {
  return (
    <div>
      <a href={props.path}>
        <div className="banner1" style={{ display: "flex" }}>
          <div
            data-aos="fade-right"
            style={{
              width: "60%",
              height: "90%",
              zIndex: "100",
              marginTop: "0",
            }}
            className={props.trapeziumClass}
          >
            <h1 className="slogan2">{props.text}</h1>
          </div>
          <div
            data-aos="fade-left"
            style={{ width: "55%", marginLeft: "-10%", marginTop: "1.3%" }}
          >
            <img
              style={{ width: "112%", height: "85%", zIndex: "-1", marginTop: '1vw' }}
              className={props.imgClass}
              src={props.imgSrc}
              alt=""
            />
          </div>
        </div>
      </a>
    </div>
  );
}

export default Banner2;



