import React from 'react';
import { Helmet } from 'react-helmet';
import TeamCard from '../../components/TeamCard';

export default function LeadershipTeam() {
  
  const TeamArr = [
    {
      id: 1,
      name: 'Nishant Chauhan',
      post: 'Director, C.F.O & Head (Research and Development)',
      about:
        'Nishant Chauhan is the Chief Financial Officer (CFO) & Head (Research & Development) at Neem Tree Agro Solutions. A Student of Organic Agriculture & Food Business, Nishant has noteworthy experience in the field of Agriculture. He also has a solid experience of 5 years in the Management space. Mr Chauhan is an advocate of social rights and has a venture that he started named as ‘Massomyiat‘, a project as an initiative against sexual abuse on children which received many accolades from the likes of Doordarshan, India.',
      linkdin: 'https://www.linkedin.com/in/nishant-chauhan-79111a138',
      src: '/img/Leadership Team/Nishant.png',
    },
    {
      id: 2,
      name: 'Paras Jain',
      post: 'Director & C.T.O',
      about:
        'Paras Jain is the Chief Technology Officer at Neem Tree Agro Solutions. He is extremely proficient in the fields of web, application & software development. Currently an engineering student at BVCOE, Paras has substantial experience in various arenas of graphic designing, ethical hacking and Artificial Intelligence/Machine Learning and Cloud Computing. Along with these, Paras is also an avid learner and is often found discussing the current technological advancements like Internet of Things and much more.',
      linkdin: 'https://www.linkedin.com/in/parasjain221',
      src: 'img/Leadership Team/Paras.png',
    },
    {
      id: 3,
      name: 'Dhiresh',
      post: 'Director & C.E.O',
      about:
        'An engineer by profession and an entrepreneur by passion, Dhiresh is the Chief Executive Officer at Neem Tree Agro Solutions . He is a Student at NSIT and has experience in various backgrounds like management, business development, social entrepreneurship and business leadership. He also has vast knowledge of Brand Building, Brand Architecture and Corporate Communications. Dhiresh aims to build a platform for farmers to provide them ample knowledge and support to facilitate their growth and prosperity.',
      linkdin: 'https://www.linkedin.com/in/dhiresh-kumar-b535a012a',
      src: '/img/Leadership Team/Dhiresh.png',
    },
  ];

  return (
    <div>
      <Helmet>
        <title>Neem Tree &mdash; Leadership Team</title>

        <meta
          name='description'
          content='Our directors are Dhiresh (CEO) Paras Jain (CTO), and Nishant Chauhan (CFO). A fine mixture of Agriculture, Technology and Management collectively working towards making strides in the AgTech space.'
        />

        <meta
          name='twitter:description'
          content='Our directors are Dhiresh (CEO) Paras Jain (CTO), and Nishant Chauhan (CFO). A fine mixture of Agriculture, Technology and Management collectively working towards making strides in the AgTech space.'
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title='Leadership Team'
          src='https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR'
          height='0'
          width='0'
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div class='container'>
        <h1 class='text-center' style={{ fontSize: '49px' ,color:'orange'}}>
          Our Team
        </h1>
      </div>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <div class='container'>
        <div> 
          {TeamArr.map((item, pos) => {
            return (
              <TeamCard
                key={pos}
                name={item.name}
                post={item.post}
                about={item.about}
                linkdin={item.linkdin}
                src={item.src}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
}



