import React from 'react';
import { Helmet } from 'react-helmet';
import Banner1 from '../components/Banner1';
import Banner2 from '../components/Banner2';
import AboutUs from '../components/AboutUs';

import Aos from 'aos';

export default function HOME() {
  const MainBanner = {
    margin: '0',
    height: '40vh',
    backgroundImage: "url('/img/Index/banner.png')",
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
  };

  const KhetiSlogan = {
    backgroundColor: 'rgba(0, 0, 0, 0.39)',
    position: 'absolute',
    left: '-24vw',
    bottom: '10px',
    width: '42rem',
    display: 'flex',
  };

  React.useEffect(() => {
    Aos.init({ duration: 1400 });
  }, []);
  return (
    <div>
      <Helmet>
        <title>Neem Tree Agro Solutions</title>
        <meta
          name="description"
          content="We are a SaaS based AgTech Company focused on delivering economical, efficient and adaptable solutions to every farmer of the nation. We aim to be the catalyst for the growth and prosperity of the farmers and the nation. "
        />

        <link rel="shortcut icon" type="image/x-icon" href="neemLogo.png" />
        <meta property="og:image" content="" />
        <meta name="twitter:title" content="" />
        <meta property="og:type" content="website" />
        <meta name="twitter:card" content="summary_large_image" />
        <meta
          name="twitter:description"
          content="We are a SaaS based AgTech Company focused on delivering economical, efficient and adaptable solutions to every farmer of the nation. We aim to be the catalyst for the growth and prosperity of the farmers and the nation. "
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title="HOME PAGE"
          src="https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR"
          height="0"
          width="0"
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div className="row" style={MainBanner}>
        <div className="col-1 col-lg-3" style={{ padding: '0' }}>
          <div></div>
        </div>
        <div className="kheti" style={{ position: 'relative' }}>
          <div className="text-center" style={KhetiSlogan}>
            <img
              alt="banenrlogo"
              style={{
                height: '5rem',
                padding: '0 1rem 0 2rem',
                marginLeft: '0',
              }}
              src="img/NeemTreeWhite.png"
            />
            <h4
              id="text"
              style={{
                color: 'rgb(255, 255, 255)',
                fontSize: '5vmin',
                marginBottom: '0',
              }}
            >
              खेती का नया तरीका
            </h4>
          </div>
        </div>
        <div className="col-1 col-lg-3"></div>
      </div>

      <div className="row" style={{ margin: '0', height: '40vh' }}>
        <div className="col-1 col-lg-3" style={{ padding: '0' }}></div>
        <div className="col-10 col-lg-6 text-left" style={{ padding: '1' }}>
          <div id="container-banner-msg">
            We Aim to provide farmers with
            <div id="flip-word">
              <div id="word-flip"></div>
            </div>
            for enhanced quality and quantity of produce
          </div>
          <br />
          <br />
        </div>

        <div className="col-1 col-lg-3">
          <div></div>
        </div>
      </div>
      <br />
      {/* 
      <div className="banner1" style={{ display: "flex" }}>
        <div style={{ width: "55%" }}>
        <img style={{width: "100%", zIndex: "100"}} class="slide-banner-left" src="img/Banner 1-left-2.png" alt="" />
        </div>
      </div> */}
      <Banner1
        imgClass="slide-banner-left"
        imgSrc="img/Banner 1-left-2.png"
        text="DIGITALIZING AGRICULTURE ONE STEP AT A TIME"
        trapeziumClass="trapezium-right slide-banner-right"
        path="aboutUs#focusAreas"
      />
      <br />
      <br />
      <AboutUs />
      <br />
      <Banner2
        imgClass="slide-banner-right second"
        imgSrc="img/Banner 2-right-2.png"
        text="FOR THE MILLION HOPES ASSOCIATED WITH FARMING"
        trapeziumClass="trapezium-left slide-banner-left"
        path="nt-kisan#hindi"
      />

      <div className="container">
        <div className="card mb-3 border-0" style={{ maxWidth: '100%' }}>
          <div className="row no-gutters">
            <div
              className="
              col-lg-7
              pt-6 pt-lg-0
              order-2 order-lg-1
              d-flex
              flex-column
              justify-content-center
            "
            >
              <div className="card-body">
                <p
                  className="card-text text-justify"
                  style={{
                    fontSize: '22px',
                    marginRight: '20px',
                    marginTop: '40px',
                    color: 'black',
                  }}
                >
                  “Farming is a profession of hope” and India holds a record for
                  second largest agricultural land in the entire world, with
                  around 60 percent rural Indian households making their living
                  directly from agri-practices.This speaks about millions of
                  hopes associated with agriculture.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <br />
      <br />

      <Banner1
        imgClass="slide-banner-left"
        imgSrc="img/Banner 3-left-2.png"
        text="UNLOCKING A LARGER OPPORTUNITY FOR DATA"
        trapeziumClass="trapezium-right-3 slide-banner-right"
        path="ntaal"
      />

      <br />
      <br />

      <div className="container">
        <div className="card mb-3 border-0" style={{ maxWidth: '100%' }}>
          <div className="row no-gutters">
            <div
              className="
              col-lg-7
              pt-6 pt-lg-0
              order-2 order-lg-1
              d-flex
              flex-column
              justify-content-center
            "
            >
              <div className="card-body">
                <p
                  className="card-text text-justify"
                  style={{
                    fontSize: '22px',
                    marginRight: '20px',
                    marginTop: '40px',
                    color: 'black',
                  }}
                >
                  We've spent years post independence collecting volumes of data
                  about Agriculture but have had minimal focus on utilising
                  Data. Time has now come to mine valuable insights through Data
                  and make India Data rich. In order to be truly Data rich, we
                  are unlocking the hidden value from Data which will cut across
                  traditional silos and mindsets.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



