import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';

import { BrowserRouter } from 'react-router-dom';
import Preloader from './components/Preloader';
ReactDOM.render(
  <div>
    <BrowserRouter>
      <Preloader />
      <App />
    </BrowserRouter>
  </div>,

  document.getElementById('root')
);



