import React from 'react';
import { Helmet } from 'react-helmet';
export default function marketPlace() {
  return (
    <div>
      <Helmet>
        <title>Neem Tree &mdash; Market Place for Farmers</title>

        <meta
          name='description'
          content='One stop for all the farming needs. Set up with an objective to integrate the system of cold chains and efficiently tackle the issues in Supply Chain.'
        />

        <meta
          name='twitter:description'
          content='One stop for all the farming needs. Set up with an objective to integrate the system of cold chains and efficiently tackle the issues in Supply Chain.'
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title='MarketPlace'
          src='https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR'
          height='0'
          width='0'
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div>
        <br />
        <br />
        <div className='container'>
          <div className='card mb-3 border-0' style={{ maxWidth: '100%' }}>
            <div className='row no-gutters'>
              <div className='col-lg-7 pt-6 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center'>
                <div className='card-body'>
                  <div className='card-title'>
                    <h1 style={{ fontSize: '49px' }}>
                      Market Place for Farmers
                    </h1>
                    <br />
                    <h3 style={{ fontSize: '29px', color: '#ec7b10' }}>
                      Market Access and Realization
                    </h3>
                  </div>
                  <p
                    className='card-text text-justify'
                    style={{
                      fontSize: '22px',
                      marginRight: '20px',
                      marginTop: '40px',
                      color: 'black',
                    }}
                  >
                    A one stop for all, this acts as a common ground for every
                    farmer to make purchases necessary for farming such as
                    seeds, fertilizers, etc and also rent equipment such as
                    tractors.
                    <br />
                    <br />
                    Also support farmers with an integrated system of cold
                    chains, refer transport and tackle issues related to supply
                    chain.
                  </p>
                </div>
              </div>
              <div className='col-lg-5 order-1 order-lg-2 hero-img'>
                <img
                  src='img/Market Place/Market Place.svg'
                  className='card-img'
                  alt='...'
                  style={{
                    width: '90%',
                    marginRight: '30px',
                    marginTop: '30px',
                  }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



