import React from 'react';
import { Helmet } from 'react-helmet';
export default function FinTech() {
  return (
    <div>
      <Helmet>
        <meta http-equiv='x-ua-compatible' content='ie=edge' />
        <title>Neem Tree &mdash; Finance Technology</title>
        <meta
          name='description'
          content='We aspire to provide the farmers with relevant financial advice based on data backed financial technologies which will help them better manage their finances and also enable them to become financially literate. '
        />

        <meta
          name='twitter:description'
          content='We aspire to provide the farmers with relevant financial advice based on data backed financial technologies which will help them better manage their finances and also enable them to become financially literate. '
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title='FINTECH'
          src='https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR'
          height='0'
          width='0'
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <br />
      <br />

      <div className='container'>
        <div className='card mb-3 border-0' style={{ maxWidth: '100%' }}>
          <div className='row no-gutters'>
            <div className='col-lg-7 pt-6 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center'>
              <div className='card-body'>
                <div className='card-title'>
                  <h3 style={{ fontSize: '29px', color: '#ec7b10' }}>
                    <span style={{ fontSize: '49px', color: 'black' }}>
                      FinTech
                    </span>
                    <br /> Financial Aid
                  </h3>
                </div>
                <p
                  className='card-text text-justify'
                  style={{
                    fontSize: '22px',
                    marginRight: '20px',
                    marginTop: '40px',
                    color: 'black',
                  }}
                >
                  Adoption of technology and data backed financial services
                  keeping in consideration various factors for maximum output.
                  Give appropriate financial advice and provide loans.
                </p>
              </div>
            </div>
            <div className='col-lg-5 order-1 order-lg-2 hero-img'>
              <img
                src='img/FIntech/Fintech.jpg'
                className='card-img'
                alt='...'
                style={{ width: '90%', marginRight: '30px' }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



