import React from 'react';
import AboutUs from '../../components/AboutUs';
import { Helmet } from 'react-helmet';
import {HashLink as Link } from 'react-router-hash-link';
export default function Company() {
  return (
    <div>
      <Helmet>
        <title>Neem Tree &mdash; About Us</title>
        <meta
          name='description'
          content='Neem Tree Agro Solutions 
    is a SaaS based AgTech company focused on delivering economical, efficient and adaptable
    cutting edge solutions for every farmer in the nation.
    The services harness the power of data and Artificial Intelligence based decision support
    system for enhanced quality and quantity of produce.'
        />

        <meta
          name='twitter:description'
          content='Neem Tree Agro Solutions 
    is a SaaS based AgTech company focused on delivering economical, efficient and adaptable
    cutting edge solutions for every farmer in the nation.
    The services harness the power of data and Artificial Intelligence based decision support
    system for enhanced quality and quantity of produce.'
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title='About us'
          src='https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR'
          height='0'
          width='0'
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <AboutUs />

      <div
        className='container text-left'
        style={{ paddingLeft: '0px', paddingRight: '0px', margin: '0 auto' }}
      >
        <hr style={{ color: '#ec7b10' }} />
        <div
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
          }}
          className='container'
        >
          <br />
          <div
            id='myCarousel'
            className='carousel slide'
            data-ride='carousel'
            style={{ margin: '0 auto' }}
          >
            {/* Indicators */}
            <ol className='carousel-indicators'>
              <li
                data-target='#myCarousel'
                data-slide-to={0}
                className='active'
              />
              <li data-target='#myCarousel' data-slide-to={1} />
              <li data-target='#myCarousel' data-slide-to={2} />
              <li data-target='#myCarousel' data-slide-to={3} />
              <li data-target='#myCarousel' data-slide-to={4} />
              <li data-target='#myCarousel' data-slide-to={5} />
              <li data-target='#myCarousel' data-slide-to={6} />
            </ol>
            {/* Wrapper for slides */}
            <div className='carousel-inner' role='listbox'>
              <div className='item active'>
                <img
                  src='img/About Us - Menu/fwdwebsitecreatives/ads.png'
                  alt='img'
                />
              </div>
              <div className='item'>
                <img
                  src='img/About Us - Menu/fwdwebsitecreatives/Ads1.png'
                  alt='img'
                />
              </div>
              <div className='item'>
                <img
                  src='img/About Us - Menu/fwdwebsitecreatives/Ads2.png'
                  alt='img'
                />
              </div>
              <div className='item'>
                <img
                  src='img/About Us - Menu/fwdwebsitecreatives/Ads3.png'
                  alt='img'
                />
              </div>
              <div className='item'>
                <img
                  src='img/About Us - Menu/fwdwebsitecreatives/Ads4.png'
                  alt=''
                />
              </div>
              <div className='item'>
                <img
                  src='img/About Us - Menu/fwdwebsitecreatives/Ads5.png'
                  alt=''
                />
              </div>
              <div className='item'>
                <img
                  src='img/About Us - Menu/fwdwebsitecreatives/Ads6.png'
                  alt=''
                />
              </div>
              {/* Left and right controls */}
              <a
                className='left carousel-control'
                href='#myCarousel'
                role='button'
                data-slide='prev'
              >
                <span
                  className='glyphicon glyphicon-chevron-left'
                  aria-hidden='true'
                />
                <span className='sr-only'>Previous</span>
              </a>
              <a
                className='right carousel-control'
                href='#myCarousel'
                role='button'
                data-slide='next'
              >
                <span
                  className='glyphicon glyphicon-chevron-right'
                  aria-hidden='true'
                />
                <span className='sr-only'>Next</span>
              </a>
            </div>
          </div>
        </div>
      </div>

      <div id='focusarea' className='container text-left'>
        <h1 style={{ fontSize: '4rem', marginTop: '5%' }}>
          <span style={{ color: '#e76f38' }}>Our Focus </span>
          <span style={{ color: 'black' }}> Areas</span>
        </h1>
        <hr style={{ color: '#ec7b10' }} />
        <div className='footer-wrapper section-bg2'>
          <div className='footer-area'>
            <div className='container'>
              <div
                className='row d-flex justify-content-between'
                id='focus_area'
              >
                <div className='col-xl-3 col-lg-3 col-md-4 col-sm-5'>
                  <div className='single-footer-caption mb-30'>
                    <div className='footer-tittle'>
                      <div className='footer-logo mb-20'>
                        <Link to='/digitalAnalytics'>
                          <h3>Digital Analytics</h3>
                        </Link>
                        <Link to='digitalAnalytics'>
                          <h5 style={{ color: '#ec7b10' }}>
                            Digital Analytics
                          </h5>
                        </Link>
                        <br />
                        <Link to='/dml'>
                          <h3>Direct Market Linkage</h3>
                        </Link>
                        <Link to='/dml'>
                          <h5 style={{ color: '#ec7b10' }}>
                            Farm to Fork Network
                          </h5>
                        </Link>
                        <br />
                        <Link to='/FinTech'>
                          <h3>FinTech</h3>
                        </Link>
                        <Link to='/FinTech'>
                          <h5 style={{ color: '#ec7b10' }}>Financial Aid</h5>
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
                <div className='col-xl-3 col-lg-3 col-md-4 col-sm-5'>
                  <div className='single-footer-caption mb-30'>
                    <div className='footer-tittle'>
                      <div className='footer-logo mb-20'>
                        <Link to='/skillDev'>
                          <h3>Skill Development</h3>
                        </Link>
                        <Link to='/skillDev'>
                          <h5 style={{ color: '#ec7b10' }}>
                            Education, Innovation
                          </h5>
                        </Link>
                        <br />
                        <Link to='/marketPlace'>
                          <h3>Market Place for Farmers</h3>
                        </Link>
                        <Link to='/marketPlace'>
                          <h5 style={{ color: '#ec7b10' }}>
                            Market Access &amp; Realization
                          </h5>
                        </Link>
                        <br />
                        <Link to='/storage'>
                          <h3>Storage</h3>
                        </Link>
                        <Link to='/storage'>
                          <h5 style={{ color: '#ec7b10' }}>Cold Storage</h5>
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
                <div className='col-xl-3 col-lg-3 col-md-4 col-sm-5'>
                  <div className='single-footer-caption mb-50'>
                    <div className='footer-title'>
                      <Link to='/farmUncle'>
                        <h3>Farm Uncle</h3>
                      </Link>
                      <Link to='/farmUncle'>
                        <h5 style={{ color: '#ec7b10' }}>
                          Service for Urban Customer
                        </h5>
                      </Link>
                      <br />
                      <Link to='/nt-kisan'>
                        <h3>Neem Tree</h3>
                      </Link>
                      <Link to='/nt-kisan'>
                        <h5 style={{ color: '#ec7b10' }}>
                          Service for Farmers
                        </h5>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



