import React, { useState } from "react";
// import NTKISANBACKEND from '../../../assets/js/nt-kisan-backend';
import { Helmet } from "react-helmet";
export default function NTKisan() {
  const [language, setLanguage] = useState(false);

  return (
    <div>
      <Helmet>
        <meta charset="utf-8" />
        <meta http-equiv="x-ua-compatible" content="ie=edge" />
        <title>NT-KISAN - Farming Application</title>
        <meta
          name="description"
          content="An AI enabled, data driven application developed to benefit the farmers of the country. The core service being the crop enhancement system which after the analysis of the parameters such as weather, soil quality etc, suggests the farmers with the most suitable seed that will give maximum yield and also guidance at every step of the way  until the harvest is produced."
        />

        <meta property="og:image" content="" />
        <meta name="twitter:title" content="" />
        <meta property="og:type" content="website" />
        <meta name="twitter:card" content="summary_large_image" />
        <meta
          name="twitter:description"
          content="An AI enabled, data driven application developed to benefit the farmers of the country. The core service being the crop enhancement system which after the analysis of the parameters such as weather, soil quality etc, suggests the farmers with the most suitable seed that will give maximum yield and also guidance at every step of the way  until the harvest is produced."
        />
      </Helmet>
      <br />
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title="NT KISAN"
          src="https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR"
          height="0"
          width="0"
          style={{ display: "none", visibility: "hidden" }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div className="container text-center">
        <div
          style={{
            margin: "auto",
            marginTop: "1.5vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <h1 style={{ color: "#000" }}>हिंदी</h1>
          <div className="toggle-container">
            <input
              type="checkbox"
              id="good"
              className="toggle"
              onChange={() => {
                setLanguage(!language);
              }}
            />
            <label htmlFor="good" id="ball" className="label">
              <div className="ball" />
            </label>
          </div>
          <h1 style={{ color: "#000" }}>English</h1>
        </div>
      </div>
      <br />
      <br />
      {/* HINDI */}

      {/* ENGLISH */}
      <div className="container text-center">
        {language === false ? (
          <h1 className="kheti" id="hindi-text">
            खेती का नया तरीका
          </h1>
        ) : (
          <h1 className="kheti" id="english-text">
            Kheti ka naya tareeka
          </h1>
        )}
        {/* css in bootstrap.min*/}
        <br />
        <br />
        <div>
          {language === false ? (
            <p className="hindi hindi-text">
              NT- KISAN एप्लिकेशन के निर्माण से हमारा उद्देश्य है कि हम प्रत्येक
              किसान कि खेती का समावेशी विकास करने में सहायता करें। <br />
              <span className="download">
                आज ही <span style={{ color: "#ec7b10" }}>मुफ्त</span> में आपने
                फोन पर डाउनलोड कीजिए NT KISAN का फ्री एप।
              </span>
              <br />
              और अपनाइए खेती का नया और बेहतर तरीका और आसान बनाइए अपनी ज़िंदगी
              को।
            </p>
          ) : (
            <p className="english-text english">
              The purpose behind the launch of NT-KISAN application is to ensure
              that it becomes the catalyst for the growth and well-being of
              every farmer in the country. <br />
              <span className="download">
                Download the NT-KISAN app for
                <span style={{ color: "#ec7b10" }}> free </span>on your phones
                today!!!
              </span>
              <br />
              Switch to a new and better method of farming and make your life
              easier.
            </p>
          )}
        </div>
        <br />
        <br />
        {/* sreenshots */}
      </div>
      <div className="container text-center">
        <h2 style={{ fontSize: "40px" }}>
          <span style={{ color: "#ec7b10" }}>NT KISAN</span>
        </h2>
      </div>
      <br />
      <div className="container">
        {language === false ? (
          <div
            className=" text-center"
            style={{
              color: "#856404",
              backgroundColor: "#fff3cd",
              borderColor: "#ffeeba",
              position: "relative",
              padding: "0.75rem 1.25rem",
              marginBottom: "1rem",
              border: "1px solid transparent",
              borderRadius: "0.25rem",
            }}
            role="alert"
          >
            एनटी किसान ऐप जल्द ही रिलीज हो रहा है!!
            <br />
          </div>
        ) : (
          <div
            className=" text-center"
            style={{
              color: "#856404",
              backgroundColor: "#fff3cd",
              borderColor: "#ffeeba",
              position: "relative",
              padding: "0.75rem 1.25rem",
              marginBottom: "1rem",
              border: "1px solid transparent",
              borderRadius: "0.25rem",
            }}
            role="alert"
          >
            The Application Is Releasing Soon!!
            <br />
          </div>
        )}
      </div>
      <div class="container">
        <div className="card mb-3 border-0" style={{ maxWidth: "100%" }}>
          <div class="row no-gutters">
            <div class="col-sm"></div>
            <div
              class="col-sm hero-img"
              style={{ margin: "auto", width: "100%" }}
            >
              <img
                src="img/kisan/Line.png"
                className="card-img kisan-mob"
                alt="..."
                style={{ margin: "auto", width: "100%" }}
              />
            </div>
            <div class="col-sm"></div>
          </div>
        </div>
      </div>
      <br />
      {/* key features */}
      <div className="container">
        {language === false ? (
          <p
            className="hindi-text"
            style={{ color: "#ec7b10", fontSize: "4.5rem" }}
          >
            विशेषताएं
            <br />
          </p>
        ) : (
          <p
            className="english-text "
            style={{ color: "#ec7b10", fontSize: "4.5rem" }}
          >
            Features:
            <br />
          </p>
        )}
        {language === false ? (
          <p className="card-text text-justify hindi hindi-text">
            1. अपने खेत को विनाशकारी कीट से मुक्त कराएं <br />
            2. अपनी फसल की पैदावार बढ़ाए
            <br />
            3. उगाईं फसल पर अपने कृषि का उचित लाभ पाइए
            <br />
            4. अपनी उपज की गुणवत्ता बढ़ाए
            <br />
            <span
              className="card-text text-justify hindi"
              style={{ color: "#ec7b10" }}
            >
              <br /> कैसे?
              <br />
            </span>
            डाउनलोड कीजिए फ्री NT-KISAN एप ओर जानिए।
          </p>
        ) : (
          <p className="card-text text-justify english english-text ">
            1. Free your crops from PEST <br />
            2. Increase in the quantity of your produce <br />
            3. Avail fair prices for your harvest and take maximum advantage of
            it <br />
            4. Increase in the quality of the harvested crops <br />
            <span
              className="card-text text-justify hindi"
              style={{ color: "#ec7b10" }}
            >
              <br /> How?
              <br />
            </span>
            Download the NT-KISAN app for free and find out.
          </p>
        )}
      </div>
      <div
        className="container"
        style={{
          backgroundImage:
            "linear-gradient(rgba(187, 230, 0, 0.5), rgba(255, 221, 28, 0.733))",
            display: 'flex'
        }}
      >
        <div style={{marginTop: "15px"}}>
          <i
            className="fa fa-phone"
            style={{
              fontSize: "48px",
              color: "rgb(255, 158, 13)",
              padding: "10px",
            }}
          />
        </div>
        <input
          type="tel"
          id="id"
          className="hindi-text"
          placeholder={
            language === false
              ? "अपना फोन नंबर डालें "
              : "Enter you phone number "
          }
          style={{
            border: "1px solid #000",
            color: "#000",
            padding: "15px 15px",
            fontSize: "15px",
            height: "100%",
            marginTop: "25px"
          }}
        />
        <button
          style={{
            border: "2px solid #fff",
            color: "black",
            backgroundColor: "rgba(255, 153, 51, 0.5)",
            margin: "1em 1em",
            padding: "0.6em",
            textAlign: "center",
            textDecoration: "none",
            display: "inline-block",
            fontSize: "2rem",
          }}
          className="cert_btn"
          id="submit-btn"
          // onClick={NTKISANBACKEND()}
        >
          {language === false ? "ठीक है " : "SUBMIT"}
        </button>
      </div>

      <div className="container text-left" id="hindi">
        {language === false ? (
          <p className="card-text text-justify hindi hindi-text">
            जैसे एक पौधे को वृक्ष बनने के लिए पानी की आवश्यकता होती है उसी तरह
            देश का किसान, भारत नाम के पेड़ को दिए पानी के समान है। यह अमृत उस
            पेड़ को रोज सहारा देते हुए उसे संपूर्ण बनाता है । इसी तरह देश का हर
            एक किसान हमेशा देश के विकास के लिए काम करते हुए अपनी फसल के द्वारा
            हर एक नागरिक को तृप्त करता है। जब व्यवसाय से आधुनिकता जुड़ेगी तब
            केवल हमारा ही नही पर किसानों का भी नाम रोशन होगा। यह Neem Tree Agro
            Solutions की एक कोशिश है जो सभी किसानों के विकास से साथ ही सफल होगी।
          </p>
        ) : (
          <p className="card-text text-justify english english-text ">
            Water is an essential element for a sapling to become a tree.
            Similarly for the growth and development of India, a farmer plays a
            crucial role. A farmer works tirelessly for the betterment of the
            country and through his harvest, fills the stomach of every Citizen.
            When Technology adds to Agriculture, It not only leads to prosperity
            of the citizens but also the farmers. Betterment of the country and
            it's farmers is our goal at Neem Tree Agro Solutions which will
            attain its objective with the collective efforts of each and every
            farmer.
          </p>
        )}
      </div>
      <div className="container text-left" id="gallery">
        <h1 style={{ fontSize: "3.5rem", marginTop: "5%", color: "#ff9933" }}>
          Testimonials
        </h1>
        <hr style={{ color: "#ec7b10" }} />
        {/*image slider start*/}
        <div className="gallery-slider">
          <div className="gallery-slides">
            {/*radio buttons start*/}
            <input type="radio" name="radio-btn" id="radio1" />
            {/*radio buttons end*/}
            {/*slide images start*/}
            <div className="gallery-slide first">
              <img src="img/Testimonials/nt-kisan/5.png" alt="" />
            </div>
            {/*slide images end*/}
            {/*automatic navigation start*/}
            <div className="navigation-auto">
              <div className="auto-btn1" />
            </div>
            {/*automatic navigation end*/}
          </div>
          {/*manual navigation start*/}
          <div className="navigation-manual">
            <label htmlFor="radio1" className="manual-btn" />
          </div>
          {/*manual navigation end*/}
        </div>
        {/*image slider end*/}
      </div>
    </div>
  );
}



