import React from 'react';
import { Helmet } from 'react-helmet';
export default function digitalAnalytics() {
  return (
    <div>
      <Helmet>
        <title>Neem Tree &mdash; Digital Analytics</title>
        <meta
          name='description'
          content="As the adaption of the digital mode gains momentum it is important that a technology enabled approach is followed for farming management in order to optimise it's operation along with making better output predictions and reducing the risk factor which will lead to reductions in cost."
        />

        <meta
          name='twitter:description'
          content="As the adaption of the digital mode gains momentum it is important that a technology enabled approach is followed for farming management in order to optimise it's operation along with making better output predictions and reducing the risk factor which will lead to reductions in cost."
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title='DigitalAnalytics'
          src='https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR'
          height='0'
          width='0'
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div>
        <br />
        <br />
        <div className='container '>
          <br />
          <br />
          <br />
          <h1 style={{ fontSize: '49px' }}>
            <span style={{ color: 'black' }}>Digital and Analytics</span>{' '}
          </h1>
          <h1 style={{ fontSize: '29px' }}>
            <span style={{ color: '#e76f38' }}>Pest and Yield Analysis</span>{' '}
          </h1>
          <hr style={{ color: '#ec7b10' }} />
          {/*<h3 class="text-left text-justify" style="line-height: 1.6;font-size:20px;">*/}
          <p
            className='card-text text-justify'
            style={{
              fontSize: '22px',
              marginRight: '20px',
              marginTop: '40px',
              color: 'black',
            }}
          >
            India has ~30 million farmers who own smartphones, which is expected
            to grow 3 times by 2020. Digital farming and connected farm services
            can impact 70 million Indian farmers. A technology- enabled approach
            to farming management that observes, measures, and analyzes the
            needs of individual fields and crops and uses the data in a smart
            way to optimize farming operations. Good digital usage and analytics
            helps in better output prediction and quality maximization and helps
            in risk management. It’ll help to enhance preventive care and
            increase producer-consumer satisfaction. AI-enabled robots for
            harvesting can lead to huge cost savings by reducing the need for
            approximately 4 agricultural laborers per acre of land.
          </p>
          <br />
          <div style={{ textAlign: 'center' }}>
            <ul className='submenu'>
              <li>
                <a
                  href='/nt-kisan'
                  style={{
                    border: '3px solid #dad8d8',
                    background: '#90EE90',
                    padding: '15px',
                    color: 'black',
                  }}
                >
                  Service for Farmers
                </a>
              </li>
            </ul>
          </div>
          <hr />
        </div>
      </div>
    </div>
  );
}



