import React from 'react';
import {Helmet} from 'react-helmet';
export default function InvestorPanel() {
  return (
    <div>
    <Helmet>
    <title>Neem Tree &mdash; Investors Panel</title>
    <meta name="description" content="A company established with a mission to digitise Agriculture, We at Neem Tree focus on the efficient use of data to provide the farmers with a decision support system, making us one of the Top AgTech companies with the values and interest of the farmer rooted within. We look forward to connecting with leading and potential investors and in jointly taking the path to better the practices of farming."/>

  
    <meta name="twitter:description" content="A company established with a mission to digitise Agriculture, We at Neem Tree focus on the efficient use of data to provide the farmers with a decision support system, making us one of the Top AgTech companies with the values and interest of the farmer rooted within. We look forward to connecting with leading and potential investors and in jointly taking the path to better the practices of farming."/>
    </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript
      ><iframe
      title="Investor Panel"
        src="https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR"
        height="0"
        width="0"
        style={{display: "none", visibility: "hidden"}}
      ></iframe></noscript>
        {/* <!-- End Google Tag Manager (noscript) --> */}
      <div>
        <div>
          <div className='container text-center'>
            <h1 style={{ fontSize: '69px' }}>
              <span style={{ color: '#e76f38' }}>Investor</span> -{' '}
              <span style={{ color: 'black' }}>Panel</span>
            </h1>
            <br />
            <img
              src='img/Investors Panel.svg'
              alt='NTinvestorimg'
              className='img-fluid'
              style={{ width: '800px' }}
            />
            <br />
            <hr style={{ color: '#ec7b10' }} />
            {/* <h3 class="card-text text-left text-justify" style="line-height: 1.6;font-size:20px;">justify paragraph is remaining */}
            <p
              className='card-text text-justify'
              style={{
                fontSize: '22px',
                marginRight: '20px',
                marginTop: '40px',
                color: 'black',
              }}
            >
              Dear Investor,
              <br />
              <br />
              In agriculture, India is a land of contradictions. The country
              produces 11 percent of total global agriculture and at the same
              time host to the world's largest number of malnourished people.
              Agriculture provides a livelihood for about half of the Indian
              population most of whom are smallholder farmers yet a majority of
              government agricultural subsidies are used by medium and large
              scale farmers. The average debt of farming household has been
              increased by nearly 5 folds in a decade and more than 3,00,000
              Indian farmers have committed suicide. <br />
              <br />
              All the major revolutions were either farm-centric or production
              centric with barely any farmer income increment for the farmer.{' '}
              <br />
              <br />
              Neem Tree Agro Solutions is a company established with a mission
              to provide farmers with knowledge, technology, marketing and
              extension services for improved quality and quantity of produce by
              efficient adoption of technology. The company focuses on the
              efficient use of big data to provide decision support system for
              the farmers. <br />
              <br />
              We look forward to your support and joint path ahead. <br />
              <br />
              Thanking You, <br /> Yours Sincerely <br /> Neem Tree Team
            </p>
            <hr />
          </div>
          <div className='container'>
            <div className=' text-center'>
              <a href='mailto:neemtreeagrosolutions@gmail.com'>
                <button
                  type='button'
                  className='btn  ml-5 mr-5 aa1'
                  style={{ backgroundColor: '#9FD268' }}
                >
                  WRITE TO US
                </button>
              </a>
              <a href='https://drive.google.com/drive/folders/1KPJZ5YsGuvoUkEl4pY10mUTUMlvN_slT?usp=sharing'>
                <button
                  type='button'
                  className='btn  ml-5 mr-5 aa1'
                  style={{ backgroundColor: '#9FD268' }}
                >
                  PRESS RELEASES
                </button>
              </a>
            </div>
          </div>
          <div className='container text-left' id='awards'>
            <h1 style={{ fontSize: '40px', marginTop: '5%' }}>
              <span style={{ color: '#e76f38' }}>Corporate </span>
              <span style={{ color: 'black' }}>Awards</span>
            </h1>
            <hr style={{ color: '#ec7b10' }} />
            <ul>
              <li>
                <a
                  href='https://www.siliconindia.com/digital-magazine/agtech-startups-june-2021/#page=30'
                  className='awards'
                >
                  June 2021: 10 Best AgTech Start-ups of 2021 by Silicon India
                </a>
              </li>
            </ul>
            {/* <p class="card-text text-justify" style="font-size: 22px;margin-right:20px;margin-top:40px;color:black;"></p> */}
          </div>
          <br />
          <br />
          <div id='ourPartners' className='container'>
            <h1 style={{ fontSize: '40px', marginTop: '5%' }}>
              <span style={{ color: '#e76f38' }}>Our </span>
              <span style={{ color: 'black' }}>Partners</span>
            </h1>
            <hr style={{ color: '#ec7b10' }} />
            <br />
            <a href='https://drive.google.com/file/d/13aWMHh4v8GWM9CFRgqQkDVqCQsEXxiij/view?usp=sharing'>
              <div
                className='border-1 partner-grp'
                style={{ display: 'flex', cursor: 'pointer' }}
              >
                <div
                  style={{ width: '50%', padding: '20px' }}
                  className='text-center'
                >
                  <img
                    className='partners one'
                    src='img/Our Partners/FITT.png'
                    alt='partners'
                  />
                  <br />
                  <br />
                  <h2 style={{ color: '#000' }}>
                    Foundation of Innovation and Technology Transfer,
                    <br /> Indian Institute of Technology, Delhi
                  </h2>
                </div>
                <div
                  style={{ width: '50%', padding: '20px' }}
                  className='text-center'
                >
                  <img
                    className='partners one'
                    src='img/Our Partners/msh.png'
                    alt='partners'
                  />
                  <br />
                  <br />
                  <h2 style={{ color: '#000' }}>
                    Ministry of Electronics and Information and Technology
                    <br /> (MeitY), Government of India
                  </h2>
                </div>
              </div>
            </a>
            <br />
            <br />
            <a href='https://drive.google.com/file/d/15neElw0Roi6LjKnkJ-3gXzrZgNQYRsqi/view?usp=sharing'>
              <div
                className='border-1 partner-grp'
                style={{ display: 'flex', cursor: 'pointer' }}
              >
                <div
                  style={{ width: '50%', padding: '20px' }}
                  className='text-center'
                >
                  <img
                    className='partners two'
                    src='img/Our Partners/Nasscom Foundation.png'
                    alt='partners'
                  />
                  <br />
                  <br />
                  <h2 style={{ color: '#000' }}>NASSCOM FOUNDATION</h2>
                </div>
                <div
                  style={{ width: '50%', padding: '20px' }}
                  className='text-center'
                >
                  <img
                    className='partners two'
                    src='img/Our Partners/Ciena Spaces.png'
                    alt='partners'
                  />
                  <br />
                  <br />
                  <h2 style={{ color: '#000' }}>CIENA</h2>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}



