import React from "react";

import "../assets/css/styles.css";
import "../assets/css/bootstrap.min.css";
import "../assets/css/slicknav.css";
import "../assets/css/flaticon.css";
import "../assets/css/animated-headline.css";
import "../assets/css/fontawesome-all.min.css";
import "../assets/css/themify-icons.css";
import "../assets/css/style.css";
import "../assets/css/banners.css";
import "../assets/css/stylesheet.css";
import "../assets/css/responsive.css";

function AboutUs() {
    return (
        <div>
            <div class="container">
            <div class="card mb-3 border-0" style={{maxWidth: "100%"}}>
                <div class="row no-gutters">
                <div
                    class="
                    col-lg-7
                    pt-6 pt-lg-0
                    order-2 order-lg-1
                    d-flex
                    flex-column
                    justify-content-center
                    "
                >
                    <div class="card-body">
                    <div class="card-title">
                        <h1 style={{fontSize: "49px", color: "#ec7b10"}}>
                        About - <span style={{color: "black"}}>Us</span>
                        </h1>
                        <br />
                    </div>
                    <p
                        class="card-text text-justify"
                        style={{
                        fontSize: "22px",
                        marginRight: "2rem",
                        marginTop: "5rem",
                        color: "black"
                        }}
                    >
                        We are a SaaS based AgTech company focused on delivering
                        economical, efficient and adaptable cutting edge solutions for
                        every farmer in the nation. The services harness the power of
                        data and Artificial Intelligence based decision support system
                        for enhanced quality and quantity of produce.
                    </p>
                    </div>
                </div>
                <div class="col-lg-5 order-1 order-lg-2 hero-img">
                    <img
                    src="img/NT-Tree.png"
                    class="card-img"
                    alt="..."
                    style={{width: "100%", marginBottom: "6rem"}}
                    />
                </div>
                </div>
            </div>
            </div>
        </div>
    )
}

export default AboutUs;


