import React from 'react';
import { Helmet } from 'react-helmet';
export default function MediaKit() {
  return (
    <div>
      <Helmet>
        <title>Neem Tree &mdash; Media kit</title>
        <meta
          name="description"
          content="All the resources and information pertaining to Neem Tree Agro Solutions is available over here. Do note that no owned asset of Neem Tree can be used elsewhere without taking requisite permissions from the company. "
        />

        <meta
          name="twitter:description"
          content="All the resources and information pertaining to Neem Tree Agro Solutions is available over here. Do note that no owned asset of Neem Tree can be used elsewhere without taking requisite permissions from the company. "
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title="MediaKIt"
          src="https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR"
          height="0"
          width="0"
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div>
        <div>
          <div className="container text-left">
            <h1 style={{ fontSize: '30px', marginTop: '3%' }}>Resources</h1>
            <hr style={{ color: '#ec7b10' }} />
            <p
              className="card-text text-justify"
              style={{
                fontSize: '20px',
                marginRight: '20px',
                marginTop: '40px',
                color: 'black',
              }}
            >
              For specific inquiries, email us at connect@neemtreeagrico.in
            </p>
          </div>
          <div className="container text-left">
            <h1 style={{ fontSize: '30px', marginTop: '5%' }}>
              About Neem Tree Agro Solutions-
            </h1>
            <hr style={{ color: '#ec7b10' }} />
            <p
              className="card-text text-justify"
              style={{
                fontSize: '20px',
                marginRight: '20px',
                marginTop: '40px',
                color: 'black',
              }}
            >
              We're a SAAS based data driven AgTech company established in 2018
              with a mission to support farmers with use of technology like
              Artificial Intelligence, Machine Learning and Data Science. We’re
              incorporated as a private limited company under the Companies Act,
              2013. Neem Tree provides a data driven crop enhancement system,
              NT-Kisan. It facilitates the decision making process and helps
              farmers grow efficiently and grow more subsequently increasing the
              income of the small and marginalised farmers.
            </p>
          </div>
          <div className="container text-left">
            <h1 style={{ fontSize: '30px', marginTop: '5%' }}>Follow us at </h1>
            <hr style={{ color: '#ec7b10' }} />
            <div style={{ display: 'flex', flexWrap: 'wrap' }}>
              <a
                style={{ marginLeft: '20px' }}
                className="mediaBtn facebook"
                href="https://www.facebook.com/neemtreeagrosolutions"
              >
                Facebook
              </a>
              <a
                className="mediaBtn instagram"
                href="https://www.instagram.com/neemtreeagro/"
              >
                Instagram
              </a>
              <a
                className="mediaBtn twitter"
                href="https://twitter.com/NeemTreeAgro"
              >
                Twitter
              </a>
              <a
                className="mediaBtn linkdin"
                href="https://www.linkedin.com/company/neemtree-agro-solution"
              >
                Linkedin
              </a>
            </div>
          </div>
          <div className="container text-left">
            <h1 style={{ fontSize: '30px', marginTop: '5%' }}>Brand Assets-</h1>
            <hr style={{ color: '#ec7b10' }} />
            <a
              className="logos"
              style={{ marginLeft: '20px' }}
              href="https://drive.google.com/drive/folders/1fe7i1Xk_IFkahAd_H3CZANvDUtTKMvNO?usp=sharing"
            >
              Logos
            </a>
            <br />
            <br />
            <div style={{ marginLeft: '20px', color: 'black !important' }}>
              You’re not allowed to use our owned assets such as the Company's
              Logo without our permission. <br />
              You may be able to use the Logo after you submit a request for the
              same by contacting us. <br />
              Post the internal review and approval you may be allowed to use
              the Logo.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}



