import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';

import HOME from './Pages/Home';
import NotFoundPage from './Pages/404/404';
import NTAAL from './Pages/R&D/NTAAL';
import NTKISAN from './Pages/OurBusinesses/NTKisan/NTKisan';
import FarmUncle from './Pages/OurBusinesses/FarmUncle';
import CorporateOrders from './Pages/OurBusinesses/CorporateOrders';
import LeadershipTeam from './Pages/AboutUs/LeadershipTeam';
import InvestorPanel from './Pages/InvestorPanel/InvestorPanel';
import MediaKit from './Pages/MediaKit/MediaKit';
import Company from './Pages/AboutUs/Company';
import ContactUs from './Pages/ContactUs/ContactUs';
import Storage from './Pages/FocusAreas/storage';
import FinTech from './Pages/FocusAreas/FinTech';
import dml from './Pages/FocusAreas/dml';
import digitalAnalytics from './Pages/FocusAreas/digitalAnalytics';
import marketPlace from './Pages/FocusAreas/marketPlace';
import skillDev from './Pages/FocusAreas/skillDev';

import Header from './components/Header';
import Footer from './components/Footer';
import BackTop from './components/BackTop';
// import Preloader from './components/Preloader'

//css
import './assets/css/styles.css';
import './assets/css/bootstrap.min.css';
import './assets/css/animated-headline.css';
import './assets/css/fontawesome-all.min.css';
import './assets/css/themify-icons.css';
import './assets/css/style.css';
import './assets/css/responsive.css';
import './assets/css/banners.css';
import './assets/css/stylesheet.css';
import './assets/css/themeStyle.css';

import 'aos/dist/aos.css';
const App = () => {
  return (
    <div>
      
      <Header />
      <Switch>
        <Route exact path="/" component={HOME} />
        <Route exact path="/aboutUs" component={Company} />

        <Route exact path="/ntaal" component={NTAAL} />
        <Route exact path="/nt-kisan" component={NTKISAN} />
        <Route exact path="/farmUncle" component={FarmUncle} />
        <Route exact path="/corporateOrders" component={CorporateOrders} />
        <Route exact path="/leadershipTeam" component={LeadershipTeam} />
        <Route exact path="/investorPanel" component={InvestorPanel} />
        <Route exact path="/MediaKit" component={MediaKit} />
        <Route exact path="/contactUs" component={ContactUs} />

        <Route exact path="/storage" component={Storage} />
        <Route exact path="/fintech" component={FinTech} />
        <Route exact path="/digitalAnalytics" component={digitalAnalytics} />
        <Route exact path="/skillDev" component={skillDev} />
        <Route exact path="/dml" component={dml} />
        <Route exact path="/marketPlace" component={marketPlace} />
        <Route path="/404" component={NotFoundPage} />
        <Redirect to="/404" />
      </Switch>

      <Footer />
      <BackTop />
    </div>
  );
};

export default App;



