import React from 'react';
import { Helmet } from 'react-helmet';
export default function NTKisan() {
  return (
    <div>
      <Helmet>
        <meta charset='utf-8' />
        <meta http-equiv='x-ua-compatible' content='ie=edge' />
        <title>Neem Tree &mdash; Corporate Orders</title>
        <meta
          name='description'
          content='Reach out to us at connect@neemtreeagrico.in for queries related to corporate orders '
        />
        <meta
          name='twitter:description'
          content='Reach out to us at connect@neemtreeagrico.in for queries related to corporate orders '
        />
      </Helmet>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <noscript>
        <iframe
          title='CORPORATE ORDERS'
          src='https://www.googletagmanager.com/ns.html?id=GTM-NQJ2TWR'
          height='0'
          width='0'
          style={{ display: 'none', visibility: 'hidden' }}
        ></iframe>
      </noscript>
      {/* <!-- End Google Tag Manager (noscript) --> */}
      <div className='container text-left'>
        <h1 style={{ fontSize: '69px' }}>
          Corporate - <span style={{ color: '#e76f38' }}>Orders</span>
        </h1>
        <br />
      </div>
      <div className='container'>
        <svg height={4} width='50vw'>
          <line
            x1={0}
            y1={0}
            x2='50vw'
            y2={0}
            style={{ stroke: 'rgb(58, 184, 8)', strokeWidth: 5 }}
          />
          Sorry, your browser does not support inline SVG.
        </svg>
      </div>
      <br />
      <br />
      <br />
      <div
        className='container'
        style={{ backgroundColor: 'rgb(21, 173, 85)' }}
      >
        <div className='px-5 py-5'>
          <div className='row'>
            <div className='col-12'>
              <h2
                className='contact-title'
                style={{ fontSize: '45px', color: 'white', marginTop: '25px' }}
              >
                Fill Up Details For the Orders
              </h2>
              <br />
            </div>
            <br />
            <div className='col-lg-12'>
              <form
                className='form-contact contact_form'
                action
                method
                id='CorporateForm'
                noValidate='novalidate'
              >
                <div className='row'>
                  <div className='col-sm-6'>
                    <div className='form-group'>
                      <input
                        className='form-control valid'
                        name='name'
                        id='name'
                        type='text'
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter your name'"
                        placeholder='Enter your name'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6'>
                    <div className='form-group'>
                      <input
                        className='form-control valid'
                        name='email'
                        id='email'
                        type='email'
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter email address'"
                        placeholder='Email'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6'>
                    <div className='form-group'>
                      <input
                        className='form-control'
                        name='company'
                        id='company'
                        type='text'
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter Company Name'"
                        placeholder='Enter Company Name'
                      />
                    </div>
                  </div>
                  <div className='col-sm-6'>
                    <div className='form-group'>
                      <input
                        className='form-control valid'
                        id='phnumber'
                        type='text'
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter Phone Number'"
                        placeholder='Phone Number'
                      />
                    </div>
                  </div>
                  <div className='col-md-6'>
                    <div className='form-group'>
                      <textarea
                        className='form-control w-100'
                        name='location'
                        id='location'
                        cols={30}
                        onfocus="this.placeholder = ''"
                        onblur="this.placeholder = 'Enter location'"
                        placeholder=' Enter location'
                        defaultValue={''}
                      />
                    </div>
                  </div>
                </div>
                <div className='form-group mt-3'>
                  <button
                    type='submit'
                    className='button button-contactForm boxed-btn'
                  >
                    Send
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <br />
    </div>
  );
}



