import React from "react";
function Banner1(props) {
  return (
    <div>
      <a href={props.path}>
        {/* <div className="banner1" style={{ display: "flex" }}>
          <div style={{ width: "55%;" }}>
            <img
              style={{ width: "100%", zIndex: "100" }}
              className={props.imgClass}
              src={props.imgSrc}
              alt=""
            />
          </div>
          <div
            style={{
              width: "60%",
              height: "100%",
              marginLeft: "-10%",
              zIndex: "-1",
            }}
            className={props.trapeziumClass}
          >
            <h1 className="slogan">{props.text}</h1>
          </div>
        </div> */}
        <div className="banner1" style={{ display: "flex", margin: "0" }}>
          <div
            data-aos="fade-right"
            style={{ width: "55%", marginLeft: "0", marginTop: "1.3%" }}
          >
            <img
              style={{ width: "100%", height: "90%", zIndex: "100" }}
              className={props.imgClass}
              src={props.imgSrc}
              alt=""
            />
          </div>

          <div
          data-aos="fade-left"
            style={{
              width: "60%",
              height: "90%",
              marginLeft: "-10%",
              zIndex: "-1",
              marginTop: "1.5vw",
            }}
            className={props.trapeziumClass}
          >
            <h1 className="slogan">{props.text}</h1>
          </div>
        </div>
      </a>
    </div>
  );
}

export default Banner1;



