import React from "react";

function TeamCard(props) {
  return (
    <div>
      <div class="team">
        <img
          style={{ marginTop: "-50px", marginLeft: "-60px" }}
          src={props.src}
          class="team-img"
          alt="..."
        />
        <div class="text-left team-body">
          <h5 class="card-title team-title">{props.name}</h5>

          <div class="card-text team-post" id="nishant">
            {props.post}
          </div>

          <div class="team-about">{props.about}</div>
          <div class="card-text">
            <small class="text-muted text-linkdin" style={{ fontSize: "15px" }}>
              <a
                href={props.linkdin}
                alt={props.name}
                style={{ color: "#05690" }}
              >
                Linkedin
              </a>
            </small>
          </div>
        </div>
      </div>
      <br />
      <br />
      <br />
    </div>
  );
}

export default TeamCard;



